import { ProcessStepElement, ProcessStepElementBase } from '@jumoo/processing';
import { customElement, html } from '@umbraco-cms/backoffice/external/lit';
import { uSyncSnapshots } from '../../constants';

@customElement(uSyncSnapshots.steps.reportConfig)
export default class uSyncSnapshotsReportConfigElement
	extends ProcessStepElementBase
	implements ProcessStepElement
{
	render() {
		return !this.options?.alias ? this.renderAll() : this.renderSingle();
	}

	renderAll() {
		return html`<uui-box>
			<umb-localize key="uSyncSnapshots_allWarning"></umb-localize>
		</uui-box>`;
	}

	renderSingle() {
		return html`
			<uui-box>
				<umb-localize key="uSyncSnapshots_singleWarning"></umb-localize>
			</uui-box>
		`;
	}
}
